import React, { PureComponent } from 'react'
import draft from "./../imgs/draft.png"

export class Presales extends PureComponent {
    render() {
        return (
            <>
                <h1>Presales</h1>
                <p> We are currently finishing up illustrations, and we will submit
                    our order to publish in mid-May. We expect to receive our books in late May, and we will ship your orders from then to early June!
                </p>

                <div> <img src={draft} alt={"Cover of tinker tim"} />  </div>
            </>



        )
    }
}

export default Presales